*{
    margin:0;
    padding: 0;
}
*{
    box-sizing: border-box;
}
*::after{
    box-sizing: border-box;
}
*::before{
    box-sizing: border-box;
}
#div1{
position:fixed;
right:20px;
top:20px;
} 
#div1::before{
content:'';
display:block;
position: absolute;
}
#div1::after{
content:'';
display:block;
position: absolute;
}
#demo{
word-break: break-all;
}
@media(max-width: 500px){
#demo{
   height: 50vh;
   overflow: auto;
}
#div1{
   position: relative;
   top: 0;
   right: 0;
}
#div1Wrapper{
   height: 50vh;
}
}